.reviews__page{
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    z-index: 5;
    transition: .3s;
    background-color: #fff;
}

.reviews__active{
    
    .reviews__page{
        left: 0;
        transition: .3s;
    }
}



.reviews__form{
    height: 100%;
}

.reviews__form,
.reviews__success,
.callback__form,
.callback__success{
    position: absolute;
    left: -100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 0 13px;
    padding-bottom: 20px;
    padding-top: 29px;
    transition: .3s;

    .signup__wrapper{
        .sub__title{
            margin-top: 20px;
        }
    }

    .form__wrapper{
        margin-top: 20px;

        .form__input{

            &:not(:first-child){
                margin-top: 10px;
            }
        }
    }

    .page__footer{

        .--btn{

            &:not(:first-child){
                margin-top: 10px;
            }
        }
    }

    &.active{
        left: 0;
        transition: .3s;
    }
}