// fonts
@import 'fonts';

// reset default styles
*, 
*::after, 
*::before{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
}

a{
    text-decoration: none;
}

ul,
ol,
li{
    list-style: none;
}

img{
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6{
    font-weight: inherit;
    font-size: inherit;
}

body{
    overflow: hidden;
}

// general style for bot 
.bot{
    display: flex;
    flex-direction: column;
    border-radius: 16px;
    background-color: #fff;
    width: 100%;
    height: 700px;
    transition: .3s;
    overflow: hidden;
    position: relative;
    right: -100%;

    --solid-color: #5AD5DC;
    --solid-color-light: #9EE3ED;
    --transparent-color: #6531A2;
    --transparent-color-black: #221A33;
    --transparent-color-light: #A160ED;
    
    &.bot__active{
        right: 0;
        transition: .3s;
    }
}

.body{
    padding: 0 13px;
    padding-bottom: 20px;
    padding-top: 29px;
    height: 100%;
    position: relative;
    z-index: 1;
}

[class*="__page"]{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.diagnosis__images{
    margin: 29px 0;
    display: flex;
    justify-content: center;

    img{
        pointer-events: none;
        user-select: none;
    }
}

// buttons
.--btn{
    outline: none;
    border: none;
    font-size: 13px;
    height: 64px;
    border-radius: 100px;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    cursor: pointer;
    user-select: none;
    transition: .3s;

    &:hover{
        transition: .3s;
    }
}

.--solid{
    background: radial-gradient(45.98% 193.12% at 79.46% -78.75%, var(--solid-color) 0%, var(--solid-color) 100%);
    color: white;
    font-family: $bold;

    &:hover{
        opacity: .7;
    }
}

.--transparent{
    border: 1px solid var(--transparent-color);
    background-color: transparent;
    color: var(--transparent-color);
    font-family: $semibold;
    
    &:hover{
        background-color: var(--transparent-color);
        color: white;
    }
}

// default title & sub title
.title{
    font-size: 26px;
    color: #6531A2;
    font-family: $bold;
    line-height: 32px;
    user-select: none;
    text-align: center;
}

.sub__title{
    color: #221A33;
    font-family: $medium;
    font-size: 15px;
    line-height: 18px;
    user-select: none;
    text-align: center;
}

.sub__title__line{
    position: relative;
    padding-bottom: 21px;

    &::after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: rgba(34, 26, 51, 0.1);
    }
}

// footer
.footer__links{
    width: 100%;
    margin-top: 17px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

    .footer__links__item{
        user-select: none;
        cursor: pointer;
        color: #221A33;
        font-size: 10px;
        font-family: $regular;
    }
}

// scroll bar
::-webkit-scrollbar{
    width: 3px;
    background-color: transparent;
}
::-webkit-scrollbar-thumb{
    background-color: #F2F5FA;
    border-radius: 16px;
    transition: .3s;
}

::-webkit-scrollbar-thumb:hover{
    background-color: #dddfe4;
    transition: .3s;
}

// components
@import "./components/header";
@import "./components/main";
@import "./components/settings";
@import "./components/closeBot";
@import "./components/clinic";
@import "./components/diagnostic";
@import "./components/reviews";
@import "./components/callback";