.clinic__active{

    .main__page,
    .diagnostic__page{
        position: absolute;
    }
    
    .clinic__page{
        left: 0;
    }
}

.clinic__page{
    position: relative;
    left: calc(-100% - 26px);
    transition: left .3s;
}

// =========== choice clinic ===========
.clinic__choice__wrapp{
    overflow: auto;
    height: 445px;
    margin: 22px 0;
    padding-right: 9px;
}

.clinic__choice__item{
    display: flex;
    align-items: center;
    padding: 13px 16px;
    width: 100%;
    justify-content: space-between;
    background-color: #F2F5FA;
    border-radius: 16px;
    height: 100px;
    cursor: pointer;
    transition: .3s;

    &:hover{
        background-color: #dddfe4;
        transition: .3s;
    }

    &:not(:first-child){
        margin-top: 15px;
    }

    .item__text{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .item__btn{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
    }

}

p.clinic__name{
    font-family: $semibold;
    font-size: 15px;
    color: #221A33;
    margin-bottom: 12px;
    user-select: none;
}

p.clinic__adress,
p.clinic__timetable{
    text-align: left;
    user-select: none;
}

.item__btn{
    .--btn{
        height: 24px;
        min-width: 60px;
        width: 100%;
        padding: 0 12px;
        font-family: $bold;

    }
}

// ============ clinic page ============
.clinic__item__wrapp{
    height: 100%;
    border-radius: 16px;
    border: 1px solid rgba(34, 26, 51, 0.1);
    margin-bottom: 20px;
}

.clinic__item{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 18px 15px;

    &:not(.active){
        display: none;
    }

    p.clinic__item__title{
        font-size: 15px;
        font-family: $medium;
        color: #221A33;
        user-select: none;
    }
}

.clinic__item__header{
    p.clinic__name{
        font-size: 18px;
    }

    .--btn{
        height: 28px;
        width: auto;
        font-family: $bold;
        padding: 0 12px;
        margin-top: 10px;
    }
}

.clinic__item__rating,
.clinic__item__reviews{
    margin-top: 30px;
}

.clinic__rating__wrapp{
    margin-top: 16px;
    width: 160px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    span{
        width: 24px;
        height: 24px;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            width: 8.5px;
            height: 100%;
            background: #e6e9ee;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
        }

        &::before{
            content: '';
            position: absolute;
            height: 8.5px;
            width: 100%;
            background: #e6e9ee;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }

        &.active{
            &::after,
            &::before{
                background: linear-gradient(180deg, var(--transparent-light) 0%, var(--transparent-color) 100%);
            }
        }
    }
}

.clinic__reviews__wrapp{
    display: flex;
    margin-top: 16px;
    flex-direction: column;

    .clinic__reviews__item{
        width: 100%;
        height: 42px;
        background-color: #F7FAFF;
        border-radius: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .3s;

        &:hover{
            background-color: #e6e9f0;
            transition: .3s;
        }

        &:not(:first-child){
            margin-top: 10px;
        }
    }
}