.main__active{

    .clinic__page,
    .diagnostic__page{
        position: absolute;
    }
    
    .main__page{
        left: 0;
    }
}

.main__page{
    position: relative;
    left: calc(-100% - 26px);
    transition: left .3s;

    .--solid{
        margin-bottom: 10px;
    }
}