.settings__menu{
    width: 100%;
    height: 0;
    background-color: var(--transparent-color-black);
    position: absolute;
    top: -100%;
    left: 0;
    z-index: 2;
    transition: .3s;
    padding: 0 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.settings__menu__active{

    .settings__menu{
        height: 100%;
        top: 0;
        transition: .3s;
    }

    .header__left__side, .header__right__side{

        .header__logo, .btn__settings{
            display: none;
        }
    }

    .header{
        position: relative;
        z-index: 3;
        background: transparent;
    }
}

.settings__logo{
    margin-bottom: 30px;

    img{
        pointer-events: none;
        user-select: none;
    }
}

.settings__wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;

    .settings__item{
        color: white;
        font-family: $bold;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;

        span{
            margin-right: 10px;
            width: 11px;
            height: 11px;

            img{
                width: 100%;
                height: 100%;
                object-fit: 100%;
            }
        }

        &:not(:first-child){
            margin-top: 10px;
        }
    }
}

.settings__content{
    width: 100%;
    height: calc(100% - 80px);
    background-color: #fff;
    position: absolute;
    z-index: 2;
    left: 0;
    top: 80px;
    display: none;
}

.settings__content__active{
    .settings__content{
        display: flex;
    }
}

.settings__content__item{
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0 13px;
    padding-bottom: 20px;
    padding-top: 29px;
    flex-direction: column;
    justify-content: space-between;

    &:not(.active){
        display: none;
    }
}

// настройки
.settings__content__item[settings]{
    justify-content: space-between;
}

.choice__color__wrapper{
    padding-top: 30px;
    padding-bottom: 40px;
}

.color__input__wrapper{
    width: 100%;
    height: 64px;
    background: #F2F5FA;
    border-radius: 16px;
    overflow: hidden;
    margin-top: 15px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &::after{
        position: absolute;
        content: '';
        background: #F2F5FA;
        z-index: 5;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        pointer-events: none;
    }

    .color__value{
        position: relative;
        z-index: 10;
        user-select: text;
    }

    input{
        border: none;
        background: transparent;
        width: 95%;
        height: 95%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;

        &::-webkit-color-swatch{
            border: 0;
            background: transparent;
            padding: 0;
        }

        &::-webkit-color-swatch-wrapper{
            padding: 0;
            background: transparent;
        }

        &:-internal-autofill-selected {
            background-color: inherit;
            background-image: none !important;
            color: inherit;
        }
    }
}


.choice__color__item{
    &:not(:first-child){
        margin-top: 20px;
    }
}

.color__radio__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.color__radio__wrapper{

    label.input__radio__wrapper{

        &.item1 input[name="solid__color"]::after{background-color: #5AD5DC;}
        &.item2 input[name="solid__color"]::after{background-color: #9BB8FF;}
        &.item3 input[name="solid__color"]::after{background-color: #874BCC;}
        &.item4 input[name="solid__color"]::after{background-color: #DCB05A;}
        &.item5 input[name="solid__color"]::after{background-color: #FF6868;}
        &.item6 input[name="solid__color"]::after{background-color: #1A2233;}
        &.item7 input[name="solid__color"]::after{background-color: #C4C4C4;}

        &.item1 input[name="transparent__color"]::before{border-color: #5AD5DC;}
        &.item2 input[name="transparent__color"]::before{border-color: #9BB8FF;}
        &.item3 input[name="transparent__color"]::before{border-color: #874BCC;}
        &.item4 input[name="transparent__color"]::before{border-color: #DCB05A;}
        &.item5 input[name="transparent__color"]::before{border-color: #FF6868;}
        &.item6 input[name="transparent__color"]::before{border-color: #1A2233;}
        &.item7 input[name="transparent__color"]::before{border-color: #C4C4C4;}
        

        input{
            position: relative;
            width: 20px;
            height: 20px;
            cursor: pointer;

            &[name="transparent__color"]{
                
                &::after{
                    background-color: #221A33;
                    opacity: 0.3;
                    width: 0;
                    height: 0;
                    transition: .3s;
                }
                &::before{
                    content: '';
                    width: 22px;
                    height: 22px;
                    border-radius: 50%;
                    border: 2px solid #221A33;
                    background-color: #fff;
                    z-index: 5;
                    opacity: 1;
                }
            }

            &::after{
                content: '';
                width: 21px;
                height: 21px;
                border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 5;
            }

            &::before{
                content: '';
                width: 0;
                height: 0;
                border-radius: 50%;
                border: 1.5px solid #221A33;
                background-color: #fff;
                opacity: 0.15;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 4;
                transition: .3s ease;
            }

        }

        &.checked{
            input[name="solid__color"]{
                &::before{
                    width: 30px;
                    height: 30px;
                    transition: .3s ease;
                }
            }

            input[name="transparent__color"]{  
                
                &::after{
                    width: 8px;
                    height: 8px;
                    transition: .3s ease;
                }
            }
        }
    }
}

.content__wrapper{

    .form__wrapper{

        .sub__title{
            margin-top: 20px;
        }

        .form__input{
            position: relative;
            
            input{
                font-family: $medium;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                padding: 0px 68px;
            }

            &:hover{
                .copy__input__code::after{
                    background-color: #dfe5ee;
                }
            }

            .copy__input__code{
                position: absolute;
                z-index: 10;
                width: 16px;
                height: 16px;
                top: 50%;
                transform: translateY(-50%);
                right: 20px;
                cursor: pointer;

                &::after{
                    content: '';
                    width: 12px;
                    height: 12px;
                    position: absolute;
                    background-color: #F2F5FA;
                    border: 1.5px solid var(--transparent-color);
                    border-radius: 4px;
                    top: 0;
                    left: 0;
                }

                &::before{
                    content: '';
                    width: 12px;
                    height: 12px;
                    position: absolute;
                    background-color: transparent;
                    border: 1.5px solid var(--transparent-color);
                    border-radius: 4px;
                    bottom: 0;
                    right: 0;
                }
            }
        }
    }
}

// патент
.settings__content__item[patent]{
    p.sub__title{
        margin-top: 20px;
    }
}

// о разработчике
.settings__content__item[developer]{
    p.sub__title{
        margin-top: 20px;
    }
}

// политика конфендициальности
.settings__content__item[policy]{
    p.sub__title{
        margin-top: 20px;
    }
}