.diagnostic__active{

    .main__page,
    .clinic__page{
        position: absolute;
    }
    
    .diagnostic__page{
        left: 0;
    }
}

.diagnostic__page{
    position: relative;
    left: calc(-100% - 26px);
    transition: left .3s;
}

.diagnostic__step{
    height: 100%;
    display: flex;
    left: -150%;
    position: absolute;
    flex-direction: column;
    justify-content: space-between;
    transition: .3s;
    width: 100%;

    &[active]{
        left: 0;
        transition: .3s;
    }

    .page__footer{
        .--btn:not(:first-child){
            margin-top: 10px;
        }
    }
}

.diagnostic__header{

    p.sub__title{
        margin-top: 20px;
    }
}

.diagnostic__checkbox__wrapp{
    display: grid;
    grid-template-columns: 140px 140px;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.diagnostic__checkbox__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: relative;
    margin-top: 15px;
}

p.diagnostic__checkbox__title{
    font-size: 13px;
    color: #221A33;
    font-family: $medium;
    user-select: none;
}

.diagnostic__checkbox{
    display: flex;
    justify-content: flex-end;
    width: 14px;
    height: 14px;
    position: relative;
    z-index: 4;
    cursor: pointer;

    input{
        position: relative;
        cursor: pointer;
        display: none;
    }

    &::before{
        content: '';
        position: absolute;
        border: 1px solid var(--transparent-color-black);
        width: 14px;
        height: 14px;
        border-radius: 4px;
        z-index: 4;
        top: 50%;
        transform: translateY(-50%);
        transition: .3s;
    }

    &::after{
        content: '';
        position: absolute;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: transparent;
        transition: .3s;
    }

    &.checked{
        
        &::before{
            border-color: var(--transparent-color);
            transition: .3s;
        }

        &::after{
            background-color: var(--transparent-color);
            transition: .3s;
        }
    }
}

.diagnostic__results,
.diagnostic__signup{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    .page__footer{
        .--btn{
            &:not(:first-child){
                margin-top: 10px;
            }
        }
    }
}

.diagnostic__signup{
    p.sub__title{
        margin-top: 20px;
    }

    .form__wrapper{
        margin-top: 20px;
    }
}

.form__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;

    .diagnostic__checkbox__item{
        width: 100%;
        justify-content: flex-start;

        .diagnostic__checkbox{
            width: 100%;
            justify-content: flex-start;

            &::after{
                left: 4px;
                transform: translate(0, -50%);
            }
        }

        p.diagnostic__checkbox__title{
            font-size: 11px;
            margin-left: 25px;
            position: absolute;
        }
    }

    .textarea__size{
        height: 250px!important;
    }

    .form__input{
        width: 100%;
        height: 64px;
        background: #F2F5FA;
        border-radius: 16px;
        transition: .3s;
        overflow: hidden;

        &:hover{
            background-color: #dfe5ee;
            transition: .3s;
        }

        &:not(:first-child){
            margin-top: 10px;
        }

        textarea{
            width: 100%;
            height: 100%;
            border: none;
            font-size: 15px;
            font-family: $semibold;
            color: #221A33;
            background-color: transparent;
            resize: none;
            padding: 23px 13px;

            &::placeholder{
                font-family: $regular;
                color: #221A33;
                user-select: none;
            }
        }

        input{
            width: 100%;
            height: 100%;
            border: none;
            outline: none;
            text-align: center;
            font-size: 15px;
            font-family: $semibold;
            color: #221A33;
            background-color: transparent;

            &::placeholder{
                font-family: $regular;
                color: #221A33;
                user-select: none;
            }
        }
    }
}

.diagnostic__results{
    display: none;

    &.active{
        display: flex;
    }
}