.close__bot__active{
    
    .close__bot{
        transition: .3s;
        top: 0;
    }
}

.diagnostic__back__active{
    .diagnostic__back{
        transition: .3s;
        top: 0;
    }
}

.close__bot,
.diagnostic__back{
    width: 100%;
    height: 100%;
    position: absolute;
    top: -100%;
    left: 0;
    z-index: 10;
    background-color: rgba(173, 172, 172, 0.77);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 13px;
    transition: .3s;
}

.close__bot__body{
    width: 100%;
    border-radius: 16px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 0 13px;
    padding-bottom: 20px;
    padding-top: 29px;
    overflow: hidden;

    .title{
        font-size: 24px;
    }

    .sub__title{
        font-size: 13px;
        margin-top: 5px;
    }

    .close__bot__btn{
        display: flex;
        align-items: center;
        margin-top: 30px;

        .--btn{
            height: 40px;

            &.--transparent{
                margin-left: 10px;
            }
        }
    }
}