.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 80px;
    background: radial-gradient(45.98% 193.12% at 79.46% -78.75%, var(--solid-color-light) 0%, var(--solid-color) 100%);
    border-radius: 16px;
    padding: 0 13px;
}

.header__left__side, .header__right__side{
    display: flex;
    align-items: center;
    
    div{
        user-select: none;
        
        &:nth-child(2){
            margin-left: 15px;
        }

        img{
            pointer-events: none;
        }

        &:not(.header__logo){
            cursor: pointer;
        }
    }
}